'use strict';

/*! main.js */

function $(selector, context) {
  return (context || document).querySelector(selector);
}

$.all = function (selector, context) {
  return Array.prototype.slice.call((context || document).querySelectorAll(selector));
};

var main = {
  $cell: true,
  $components: [{
    $type: 'div',
    id: 'colores',
    $components: [{
      $type: 'span', class: 'color rojo', onclick: function onclick(e) {
        $("#colores")._colorAction(this, 'activo', 'rojo');
      } }, {
      $type: 'span', class: 'color azul', onclick: function onclick(e) {
        $("#colores")._colorAction(this, 'activo', 'azul');
      } }, {
      $type: 'span', class: 'color verde', onclick: function onclick(e) {
        $("#colores")._colorAction(this, 'activo', 'verde');
      } }, {
      $type: 'span', class: 'color amarillo', onclick: function onclick(e) {
        $("#colores")._colorAction(this, 'activo', 'amarillo');
      } }, {
      $type: 'span', class: 'color blanco', onclick: function onclick(e) {
        $("#colores")._colorAction(this, 'activo', 'blanco');
      } }, {
      $type: 'span', class: 'color negro', onclick: function onclick(e) {
        $("#colores")._colorAction(this, 'activo', 'negro');
      }
    }],
    _colores: [],
    _colorAction: function _colorAction(el, className, valor) {
      var classes = el.className.split(' ');
      var existingIndex = classes.indexOf(className);
      var existingValor = this._colores.indexOf(valor);

      if (existingIndex >= 0) classes.splice(existingIndex, 1);else classes.push(className);

      if (existingValor >= 0) this._colores.splice(existingValor, 1);else this._colores.push(valor);

      el.className = classes.join(' ');
      $('#banderas')._update();
    }
  }, {
    $type: 'div',
    id: 'banderas',
    $init: function $init() {
      this.$components = flagsData.map(this._template);
    },
    _update: function _update() {
      $('#banderas .color').style.display = 'none';
      if ($('#colores')._colores.length > 0) {
        var colores = $('#colores')._colores;
        for (var i = 0; i < colores.length; i++) {
          $('#banderas .color ' + colores[i]).style.display = 'inline-block';
        }
      } else {
        $('#banderas .color').style.display = 'inline-block';
      }
    },
    _template: function _template(item) {
      return {
        $type: 'img',
        class: 'color ' + item.colores,
        src: 'img/banderas/' + item.nombre + '.png'
      };
    }
  }]
};